}
.dark button.active {
  background-color: #111;
}
.dark #top-row-nav #mine-counter,
.dark #top-row-nav #timer {
  border-color: #888;
}
.dark #playarea {
  border-color: #888;
}
.dark #playarea #column-nums {
  border-color: #888;
}
.dark #playarea #column-nums > div:not(:first-child)::after {
  background-color: #888;
}
.dark #playarea #row-nums {
  border-color: #888;
}
.dark #playarea #row-nums > div:not(:first-child)::after {
  background-color: #888;
}
.dark #playarea #board {
  border-color: #888;
}
.dark #playarea #board::-webkit-scrollbar-track {
  background-color: #8881;
